body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  background-color: #FFF;
  color: #2AA784;
}
body h1,h2,h3,h4,h5,h6 {
  font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: #2AA784;
}
body p {
  font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: #484D52;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Home {
    background-color: #f5f5f5;
}
.wrapper-selectSector {
    margin: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 95vh;
}

.wrapper-selectSector h2 {
    font-size: min(8vw, 40px);
    font-weight: bold;
    margin: 0;
    padding: 0;
}

.wrapper-selectSector-title {
    margin-top: 30px;
    margin-bottom: 0px;
}

.wrapper-selectSector-sectorsCard {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 15px;
    flex-grow: 1;
}

.wrapper-selectSector-sectorsCard-card {
    margin: 10px 0 10px 0;
    background-color: #2AA784 !important;
}

.wrapper-selectSector-sectorsCard-card-content {
    display: flex;
    flex-direction: row;
}

.wrapper-selectSector-sectorsCard-card-content-media {
    display: flex;
    width: 30%;
}

.wrapper-selectSector-sectorsCard-card-content-text {
    display: flex;
    flex-direction: column;
    width: 70%;
}

.wrapper-selectSector-sectorsCard-card-content-text h3 {
    margin: 0 15px 0 15px;
    padding: 0;
    font-size: min(5vw, 36px);
    text-transform: capitalize;
    color: #FFF;
}

.wrapper-selectSector-sectorsCard-card-content-text p {
    margin: 5px 15px 0 15px;
    font-size: min(4vw, 18px);
    color: #FFF
}

.logo {
    width: 90%;
    margin-top:15px;
    max-width: 600px;
}
.wrapper-currentStatus {
    margin: 15px;
}
.wrapper-currentStatus h2{
    font-size: min(10vw, 36px);
} 
.wrapper-timeline {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 2px solid rgb(149, 149, 149);
}
.wrapper-timeline-title h2{
    text-transform:capitalize;
    font-size: min(10vw, 36px);
    padding: 15px;
    margin: 0;
}
.wrapper-timeline-timeline {
    width: 100%;
    max-width: 600px;
    padding-bottom: 20px;
}

.wrapper-lineChart {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px 0 15px 0;
}
.wrapper-lineChart h3 {
    font-size: min(6vw, 26px);
    margin: 0;
    padding: 0;
    align-self: flex-start;
}

.wrapper-lineChart-graph {
    width: 100%;
    max-width: 400px;
}
.wrapper-PastEvents {
    margin: 0;
    padding: 0;
}
.wrapper-PastEvents h3 {
    padding: 0;
    margin: 0;
    font-size: min(6vw, 26px);
}
.wrapper-PastEvents-content {
    padding: 0;
    margin-top: 15px;
}
.wrapper-PastEvents-content p {
    padding: 0;
    margin: 5px;
}
.wrapper-decisionBasisWithGraph {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px 0 15px 0;
}
.wrapper-decisionBasisWithGraph h3 {
    font-size: min(6vw, 26px);
    margin: 0;
    padding: 0;
    align-self: flex-start;
}
.wrapper-decisionBasisWithGraph-graph {
    width: 100%;
    max-width: 400px;
}



.wrapper-decisionBasisWithText {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px 0 15px 0;
}
.wrapper-decisionBasisWithText h3 {
    font-size: min(6vw, 26px);
    margin: 0;
    padding: 0;
    align-self: flex-start;
}
.wrapper-decisionBasisWithText-text {
    width: 100%;
}



.wrapper-decisionVoteList {
    width: 100%;
}
.wrapper-decisionVoteList-buttonGroup {
    width: 100%;
}
.wrapper-decisionVoteList-button {
    height: 60px;
    justify-content: "flex-start" !important;
}
.wrapper-decisionVoteList-button p{
}
.Admin {
    background-color: red;
}
